@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
body {
  margin: 0px;
  padding: 0px;
}
.allPromoNews {
  font-family: "Inter", sans-serif;
  width: 100%;
  margin: 80px;
}
.promoBlock {
  margin-bottom: 150px;
}

.promoAndNews {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.promoH2 {
  margin-top: 224px;
  /* width: 270px; */
  font-family: "Inter", sans-serif;
  text-align: center;
  font-weight: 400;
  font-size: 60px;
}
.promoMonth {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 40px;
}

.newDate {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
}

.restName {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  color: black;
  text-decoration: underline;
}

.newSpeciality {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 40px;
}

.newsText {
  width: inherit;
  height: 57px;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}
.newsImages {
  margin-bottom: 45px;
}
.newsImage {
  width: 420px;
  height: 293px;
  background: #d9d9d9;
  margin-right: 10px;
}

.newsFooter {
  display: flex;
  justify-content: space-between;
}
.newsAbout {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 33px;
  line-height: 40px;
  text-align: center;
  color: #000000;
  text-decoration: none;
}
.newsContact {
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 33px;
  line-height: 40px;
  text-align: center;
  color: #000000;
  text-decoration: none;
}
.newsDelivery {
  width: 305px;
  height: 40px;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 33px;
  line-height: 40px;
  text-align: center;
  color: #000000;
  margin-right: 80px;
  text-decoration: none;
}

.discount {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.take-discount {
  width: 589px;
  height: 113px;
  background: #f9965e;
  border-radius: 28px;
  text-align: center;
  align-items: center;
}
.sale-discount {
  width: 460px;
  height: 39px;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 39px;
  color: #000000;
  margin: 0px;
  padding: 0px;
}

.bring-cup {
  width: 477px;
  height: 19px;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
